import Vue from 'vue';
import VueRouter from 'vue-router';

import Home from '../views/Home.vue';
import DxScheduler from '@/views/DxScheduler.vue';
import DxMultipleRecordSelection from '../views/DxMultipleRecordSelection.vue';
import TestFilter from '@/views/TestFilter.vue';

Vue.use(VueRouter);

export default new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: Home 
    },
    {
      path: '/scheduler',
      component: DxScheduler 
    },
    {
      path: '/multiple-record-selection',
      component: DxMultipleRecordSelection 
    },
    {
      path: '/test-filter',
      component: TestFilter 
    },
  ]
})
